// 
const createPost = async payload => {
	const { 
		post_id,
		comment_content
	} = payload
	const comment_type = 0
	const db = uniCloud.database()
	
	const comments = db.collection('comments')
	
	const res = comments.add({
		post_id,
		comment_content,
		comment_type
	})
	
	return res
} 

// 
const createComment = async payload => {
	const { 
		post_id,
		comment_content,
		reply_user_id,
		reply_comment_id,
	} = payload
	const comment_type = 1
	const db = uniCloud.database()
	
	const comments = db.collection('comments')
	
	const res = comments.add({
		post_id,
		comment_content,
		comment_type,
		reply_user_id,
		reply_comment_id
	})
	
	return res
} 

const praise = async payload => {
	const { uid, commentID, praiseStatus } = payload

	const res = await uniCloud.callFunction({
		name: 'index',
		data: {
			action: 'comment/praise',
			data: {
				uid,
				commentID,
				praiseStatus
			}
		}
	})

	return res
}

// 
const countCommentComment = async payload => {
	const { _id } = payload
	const db = uniCloud.database()
	const comments = db.collection('comments')
	const {result} = await comments.where(`'${_id}'==reply_comment_id`).count()
	return result.total
}

// 
const countTotalComment = async payload => {
	const { _id } = payload
	const db = uniCloud.database()
	const comments = db.collection('comments')
	const {result} = await comments.where(`'${_id}'==post_id`).count()
	return result.total
}

export default {
	praise,
	createPost,
	createComment,
	countCommentComment,
	countTotalComment
}